<template>
    <div>
        <div class="modal d-block my-popups dialog">
                    <loader :is-visible="isLoading"></loader>
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header">
                                <slot name="header"><h5 class="modal-title">{{ $t("AssociateProducts") }} </h5></slot>
                        
                                <button type="button" v-on:click="closePopup()" href="javascript:;" class="close"><em class="fa fa-times"></em></button>
                            </div>
                            <div class="modal-body">
                                <div class="form_content">
                                    <div class="col-sm-12 col-md-12 col-lg-12 form-panel p-0">
                               
                                                    <div class="row">
                                               
                                                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                                                    <div class="form-group">
                                                            
                                                                        <input class="form-control" type="text" v-model.trim="SearchItemName" :placeholder="$t('ItemName')"/>
                                                                    </div>
                                                                </div>
                                                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                                                    <div class="form-group">
                                                               
                                                                        <input class="form-control" type="text" :placeholder="$t('UniqueNameCode')" v-model.trim="SearchItemUniqueName" />
                                                                    </div>
                                                                </div>
                                                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left">
                                                                    <div class="form-group">
                                                                        <div class="search-btm-btn">
                                                                            <a href="javascript:;" v-on:click="FetchData()" class="btn btn-success mr-1"><em class="fa fa-search"></em></a>
                                                                            <a href="javascript:;" class="btn btn-danger" v-on:click="cancelOrRefresh()"><em class="fa fa-refresh"></em></a>
                                                                        </div>
                                                                    </div>
                                                       
                                                   
                                          
                                            
                                                    <div class="p-actions float-right">
                                                        <a href="javascript:void(0);" title="" id="ancmergeloaddata" v-on:click="openProductModals()" class="mr-2"><em class="fa fa-plus pr-1"></em>{{ $t("AddProduct") }} </a>
                                                        <!-- <RouterLink data-toggle-tooltip="tooltip" class="text-black" v-if="$route.params.AccountCode=='NEW_PROVIDER'"
                                                    :to="'/CRM/ManageProduct/0/'+sourceid" title="Add Product" id="addProduct"><em class="fa fa-plus pr-1"></em>Add Product
                                                </RouterLink>  -->
                                                    </div>
                                                    </div>
                                                    </div>
                                            <div id="divshowProductList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                                <div class="form-group is-invalid" v-if="isProductSelected">
                                                    <div class="invalid-feedback alert alert-danger" role="alert" style="display:block;">
                                                        {{ errorMessageForProduct }} <a href="javascript:;" v-on:click="isProductSelected = false"><em class="fa fa-close float-right text-danger"></em></a>
                                                    </div>
                                                    </div> 
                                   
                                                <div class="listing mt-2">
                                                    <div class="table-responsive tableborder popuptablescroll_365" style="max-height:550px!important;overflow-x:auto">
                                                        <table class="table-sorting table table-bordered m-0" v-if="!noRecord" aria-describedby="product">
                                                            <thead class="thead-bg">
                                                                <tr>
                                                                     <th scope="col" style="width:50px !important;" class="text-center"><input id="chkAll" class="chkitems" value="" type="checkbox"></th>
                                                                     <th scope="col"><span>{{ $t("ItemName") }} </span></th>
                                                                     <th scope="col"><span>{{ $t("ItemType") }} </span></th>
                                                                     <!-- <th scope="col"><span>{{ $t("Provider") }} </span></th>
                                                                     <th scope="col"><span>{{ $t("Price") }} </span></th> -->
                                                                     <th scope="col"><span>{{ $t("ItemCode") }} </span></th>
                                                                     <th scope="col" v-if="INVENTORY_APPLICABLE == 'yes'"><span>{{ $t("TotalItems") }} </span></th>
                                                                     <th scope="col" v-if="INVENTORY_APPLICABLE == 'yes'"><span>{{ $t("AvailableQuantity") }} </span></th>
                                                                     <th scope="col"><span>{{ $t("CreatedBy") }}</span></th>
                                                                     <th scope="col"><span>{{ $t("CreatedAt") }} </span></th>
                                                                     <th scope="col"><span>{{ $t("Status") }} </span></th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <tr v-for="(item, index) in ProductData" :key="index">
                                                                    <td class="text-center">
                                                                        <input class="chkItems" v-bind:value="item.asset_catalogue_id + (submodulecode == 'CRM_LEADS' || submodulecode == 'CRM_DEALS' ? '|' + item.AccountID : '')"  type="checkbox"></td>
                                                                    <td>{{ item.name }}</td>
                                                                    <td>{{ item.asset_type }}</td>
                                                                    <!-- <td>{{ item.ProviderName }}</td>
                                                                    <td>{{ item.unit_price }}</td> -->
                                                                    <td>{{ item.item_code }}</td>
                                                         
                                                                    <td v-if="INVENTORY_APPLICABLE == 'yes'">{{ item.number_of_item }}</td>
                                                                    <td v-if="INVENTORY_APPLICABLE == 'yes'">{{ item.available_items }}</td>
                                                                    <td>{{ item.created_by }}</td>
                                                                    <td>{{ $options.filters.formatDate(item.created_at) }}</td>
                                                                    <td><span v-bind:class="{ 'px-2 bg-success text-white d-block text-center': (item.status_id == 'Active'), 'px-2 bg-danger text-white d-block text-center': (item.status_id == 'Inactive') }">{{ item.status_id }}</span></td>
                                                                    <td style="display:none">{{ item.AccountID }}</td>
                                                                </tr>
                                                            </tbody>
                                                        </table>
                                                        <div v-if="noRecord" class="text-center text-danger" style="padding:20px;">{{ $t("NoRecordfound") }} </div>
                                                    </div>
                                                    <div class="border-top-paging" v-if="!noRecord">
                                                        
                                                <Pager v-bind:numberofrecords="TotalRecords"  :CurrentPageNumber="PageNumber" prev-text="Prev" first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
                                                       
                                                   </div>
                                                   <div>
                                                   <small class="float-right mt-2">
                                                   <a id="associateProduct" href="javascript:;" :title="$t('Associate')" v-on:click="addProduct()" class="btn btn-success text-white"><em class="fa fa-plus pr-1"></em>{{ $t("Associate") }} </a>
                                                   </small>
                                                   </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <AddProduct v-if="isaddProduct" v-on:close="isaddProduct=false;FetchData()"></AddProduct> 
    </div>
       
                    
</template>
<script>
import LeadDataService from "../../../services/DataService";
import Pager from '../../Common/Pager.vue';
import AddProduct from "../Modal/addAccountProduct.vue"
export default {
    components:{
        Pager,
        AddProduct
    },
    props: {
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
     
        companyid: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
       
    },
    data: function() {
        return {
            isLoading:false,
            ProductData: null,
            Headers: null,
            ProductCheckBoxes: [],
            SearchItemName: '',
            SearchItemUniqueName:'',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            noRecord: false,
            isProductSelected: false,
            errorMessageForProduct: '',
            checkButtonClick: false,
            INVENTORY_APPLICABLE: 'No',
            isaddProduct:false,
        }
    },
    created: function () {
        this.FetchData();
        this.GetPageSizeOption();
    },
    methods: {
        pagerMethod: function (value) {
          this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
            },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        closePopup: function () {
            this.$emit('close');
        },
        formatDate: function(value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD").format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
            }
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk =this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        SetCurrentPage: function (pageNumber) {
            this.PageNumber = pageNumber;
            if (pageNumber > this.GroupEnd) {
                this.GroupStart = pageNumber;
                this.GroupEnd = pageNumber + 2;
            }
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        SetPageSize: function (event) {
            this.PageSize = event.target.value;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        addProduct: function () {
           var vm=this;
            this.isProductSelected = false;
            this.errorMessageForProduct = '';
            var productIds = [];
            vm.checkButtonClick = true;
            $("#divshowProductList table tr td input[type='checkbox']:checked").each(function () {
               productIds.push($(this).val());
            });

            if (productIds.length > 0) {
                var ProductMappingData = {
                    sourceId: this.sourceid,
                    moduleName: this.modulename,
                    subModuleCode: this.submodulecode,
                    sourceName: this.submodulecode =='CRM_LEADS'?'lead_id': this.submodulecode =='CRM_ACCOUNTS'?'account_id': 'deal_id',
                    productIds: productIds
                };
                var objectPostString = JSON.stringify(ProductMappingData);
               LeadDataService.SaveProductForMapping(objectPostString).then(function (response) {                 
                    if (response.data[0].Status == "Success") {                      
                        vm.ShowAlert(vm.$t("Product are successfully associated"), "success", true, vm.$t("Alert"));
                        vm.$emit('close');
                        vm.$parent.loadData('products');
                        vm.checkButtonClick = false;
                        $('#tab-products-content').addClass("active show");
                        $('#tab-products').addClass("active");
                        $('#tab-Details').removeClass("active");
                        $('#tab-timeline').removeClass("active");
                        $('#tab-notes').removeClass("active");
                        $('#tab-attachment').removeClass("active");
                        $('#tab-openactivities').removeClass("active");
                        $('#tab-emails').removeClass("active");
                        $('#tab-Contact').removeClass("active");
                        $('#tab-calls').removeClass("active");
                        $('#tab-CoOwner').removeClass("active");
                        $('#tab-lead-content').removeClass("active show");
                        $('#tab-timeline-content').removeClass("active show");
                        $('#tab-notes-content').removeClass("active show");
                        $('#tab-attachment-content').removeClass("active show");
                        $('#tab-openactivities-content').removeClass("active show");
                        $('#tab-emails-content').removeClass("active show");
                        $('#tab-Contact-content').removeClass("active show");
                        $('#tab-calls-content').removeClass("active show");
                        $('#tab-CoOwner-content').removeClass("active show");
                    }                     
                });
            }
            else {
                vm.checkButtonClick = false;
                this.isProductSelected = true;
                this.errorMessageForProduct = 'Please select any product first.';
            }
        },

        FetchData: function () {
             
            var vm=this;
           vm.isLoading=true;
            var url = "id=" + this.sourceid + "&subModuleCode=" + this.submodulecode + "&itemName=" + this.SearchItemName + "&ItemUniqueName=" + this.SearchItemUniqueName + "&pageSize=" + this.PageSize + "&pageNumber=" + this.PageNumber + "&sortBy=" + this.SortBy + "&sortExp=" + this.SortExp + "&statusId=1001&requestType=popup";
            LeadDataService.getItemListForAssociate(url).then(function (response) {
            if (response.data != null && response.data != "" && response.data.data.length != 0) {
            vm.isLoading=false; 
                        vm.ProductData = response.data.data;
                        vm.TotalRecords = response.data.data[0].total_records;
                        vm.INVENTORY_APPLICABLE = response.data.data[0].INVENTORY_APPLICABLE;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vm.noRecord = false;
                         
                    }
                    else {
                        vm.isLoading=false; 
                        vm.noRecord = true;
                        vm.TotalRecords = 0
                    }
                   
                    setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                    //setTimeout("SliderDivListingScrool()", 100);
                });
        },
        cancelOrRefresh: function(){
            this.SearchItemName = "";
            this.SearchItemUniqueName = "";
            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        openProductModals:function () {
            var vm = this;
            vm.isaddProduct=true;
        },
    },
}
</script>