<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmproduct from "../../../components/Common/Modal/crmproduct.vue";
import crmcall from "../../../components/Common/Modal/crmcall.vue";
import crmsendemail from "../../../components/Common/Modal/crmsendemail.vue";
import modalproduct from "../../../components/Common/Modal/modalproduct.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modaldeploy from "../../../components/Common/Modal/modaldeploy.vue";
import modelquoteemail from "../../../components/Common/Modal/modelquoteemail.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
import purchaseorderpreview from "../../../components/Common/InvoicePreview.vue";
export default {
  components: {
    crmtimeline,
    crmnote,
    crmattachment,
    crmsendemail,
    crmproduct,
    crmcall,
    modalattachment,
    modaltask,
    modalevent,
    modalcall,
    modelquoteemail,
    modalproduct,
    modaldeploy,
    crmreadmore,
    purchaseorderpreview
  },
  data() {
    return {
      activeKey: 1,
      isNotes: false,
      isAttachment: false,
      isTask: false,
      isFollowup: false,
      isScheduleCall: false,
      isSendEmail: false,
      iscontactDetails: null,
      showContactModal: false,
      showProductModal: false,
      showAddTaskModal: false,
      showAddEventModal: false,
      showCallModal: false,
      sendEmailModal: false,
      showAttachmentModal: false,
      LeftViewData: [],
      noteData: [],
      RightViewData: null,
      attachmentData: [],
      ginData: [],
      activitiesData: null,
      timelinedata: [],
      productData: null,
      productHeaders: null,
      contactsData: null,
      contactsHeader: null,
      noRecordForTab: false,
      isShowNoteDialogue: false,
      leadsHeader: null,
      dealsHeader: null,
      dealsData: null,
      leadsData: null,
      EmailData: [],
      EmailLogData: [],
      template1: false,
      template2: false,
      template3: false,
      template4: false,
      CRMPurchaseOrder: false,
      SummaryData: [],
      TemplateName: null,
      orderNumber: "",
      createdAt: "",
      companyName: "",
      clientName: "",
      billingContactPerson: "",
      shippingContactPerson: "",
      billingFullAddress: "",
      billingAccountAddress: "",
      shippingFullAddress: "",
      shippingAccountAddress: "",
      billingCountryData: "",
      shippingCountryData: "",
      billingCityData: "",
      shippingCityData: "",
      billingStateData: "",
      shippingStateData: "",
      billingCountyData: "",
      shippingCountyData: "",
      billingZipCodeData: "",
      shippingZipCodeData: "",
      billingEmailData: "",
      shippingEmailData: "",
      billingUserPhoneData: "",
      shippingUserPhoneData: "",
      address: "",
      email: "",
      phoneNo: "",
      orderTitle: "",
      description: "",
      termAndCondition: "",
      subtotal: "",
      shippingCharges: "",
      adjustments: "",
      netTotal: "",
      callData: null,
      modalItem: null,
      quotationNumber: "--",
      noteText: "",
      SortBy: "",
      SortExp: "",
      PageNumber: 1,
      PageSize: 50,
      modalTaskItem: null,
      modalFollowUpItem: null,
      modalCallItem: null,
      modalDeleteItem: null,
      isView: false,
      modal: null,
      showAddEmailTemplate: false,
      showDeployModal: false,
      LocationSelect: null,
      stausName: "",
      ginid: null,
      NoteListType: "list",
      gin_lineItem_id: null,
      currentTab: "",
      userid: null,
      companyId: null,
      companylogo:null,
      containername: null,
      ApiURL: `id=${this.$route.params.id}&companyId=${this.companyId}}&userId=${this.userid}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      ApiURL1: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      maxsize:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
      disallowedfiletypes:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid,'CRM', 'CRMDisallowedFileTypes')",
      ApiURLEmail: `id=0&refId=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`,
      editUrl: `/CRM/ManagePurchaseOrder/${this.$route.params.id}/${this.$route.params.moduleName}/${this.$route.params.subModuleCode}`, 
      isShowNotePopup: false,
      EmailListingApiURL: `id=0&refId=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`,
      PreviewData:null  ,
      isLoading:false,
      EmailResultBody:''
    };
  },
  created: function () {
        this.userid= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.containername= this.GetUserInfo.ContainerName;
        this.companylogo= this.GetUserInfo.CompanyLogo;    
        this.FetchData();        
  }, 
  updated(){ 
    var vm=this; 
    setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
  },
  methods: {
    DisableButton: function () {
      var vm = this;
      vm.isNotes = false;
      vm.isAttachment = false;
      vm.isTask = false;
      vm.isFollowup = false;
      vm.isScheduleCall = false;
      vm.isSendEmail = false;
    },    
    openCRMAttachmentModal: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAttachment") 
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showAttachmentModal =
          this.showAttachmentModal ? false : true;
      }
    },
    openlocation: function (value) {
      if (value.gin_id != null && value.gin_id !== "undefined") {
        this.ginid = value.gin_id;
      } else {
        this.gin_lineItem_id = value;
      }
      this.showDeployModal = this.showDeployModal ? false : true;
   
       
    },
    commonApprove: function (LocationSelect) {
      this.LocationSelect = LocationSelect;
      if (this.LocationSelect != null) {
        var vm = this;
        var val = "";
        if (typeof this.ginid !== "undefined" && this.ginid != null) {
          val = this.ginid;
        } else {
          val = this.gin_lineItem_id;
        }
        if (val.length > 0 || val != "") {
          vm.confirmR(vm.$t("AreYouSureToApproveSelectedRecords"),true,null,
            function (result) {
              var postJSON = {
                ids: val,
                moduleName: "CRM",
                subModuleCode: "CRM_PURCHASE_ORDERS",
                approvaltype: "CRM_APPROVE",
                locationid: vm.LocationSelect,
                viewmode: vm.ginid == null ? "YES" : "",
              };
              var objectPostString = JSON.stringify(postJSON);
              LeadDataService.changestausInwards(objectPostString).then(
                function (response) {
                  var box = "";
                  if (response.data[0].Status == "Success") {                    
                    vm.ShowAlert("GIN Inward Approved Successfully","success",true,vm.$t("Alert"));
                    vm.loadData("GIN");
                  } else {
                    box = vm.ShowAlert(vm.$t("Error Occur while Approving Record"), "failure",true,vm.$t("Alert"));
                  }
                  setTimeout(function () {
                    box.modal("hide");
                  }, 2000);                   
                }
              );                         
            });      
        } else {
          vm.ShowAlert(vm.$t('LeaveApprove'),'success',true,vm.$t('Alert'));
        }
      } else {
        vm.ShowAlert(vm.$t('Please Select the location in order to approve'),'success',true,vm.$t('Alert'));       
      }
    },
    commonReject: function (value) {
      var vm = this;
      var val = "";
      if (value.gin_id != null && value.gin_id !== "undefined") {
        vm.ginid = value;
        val = value.gin_id;
      } else {
        vm.gin_lineItem_id = value;
        val = vm.gin_lineItem_id;
      }
      if (val.length > 0 || val != "") {
        vm.confirmR(vm.$t("AreYouSureToRejectSelectedRecords"), true, null, function (result) {
           
          var postJSON = {
            ids: val,
            moduleName: "CRM",
            subModuleCode: "CRM_PURCHASE_ORDERS",
            approvaltype: "CRM_REJECT",
            locationid: "",
            viewmode: vm.ginid == null ? "YES" : "",
          };
          var objectPostString = JSON.stringify(postJSON);
          LeadDataService.changestausInwards(objectPostString).then(function (
            response
          ) {
            var box = "";
            if (response.data[0].Status == "Success") {             
              vm.ShowAlert("GIN Inward Reject Successfully", "success", true,vm.$t("Alert"));
              vm.loadData("GIN");               
              $("#tab-GIN").trigger("click");
            } else {
              vm.ShowAlert("GIN Inward " + vm.$t("RejectError"), "failure", true,vm.$t("Alert"));
            }
            setTimeout(function () {
              box.modal("hide");
            }, 2000);             
          });           
        });
         
      } else {
        vm.ShowAlert(vm.$t("RemoveReject"));
      }
    },
    FetchData: function () {     
      var vm = this;
      vm.isLoading=true;
     LeadDataService.FormLayoutView(this.ApiURL).then(function (response) { 
        var fullLeftJSON = response.data.LEFTDATA;
        var stausName = $.grep(fullLeftJSON, function (element, index) {
          return element.name == "status_id";
        });
        if (stausName != null && stausName.length > 0) {
          vm.stausName = stausName[0];
        }
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
          return element.form_field_visibility == "YES";
        });
        if (
          response.data.RIGHTDATA != null &&
          response.data.RIGHTDATA.length > 0
        ) {
          var fullJSON = response.data.RIGHTDATA;
          var gruopBy = "";
          $.each(
            fullJSON,
            $.proxy(function (index, item) {
              gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
              var chkGroup = $.grep(
                this.timelinedata,
                function (element, index) {
                  return element.groupKey == gruopBy;
                }
              );
              if (chkGroup.length == 0) {
                var returnedData = $.grep(fullJSON, function (element, index) {
                  return (
                    vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy
                  );
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    groupKey: gruopBy,
                    groupData: returnedData,
                  };
                  vm.timelinedata.push(objGroup);
                }
              }
            }, vm)
          );
          vm.isLoading=false;
          vm.noRecordForTab = false;
        } else {
          vm.noRecordForTab = true;
        }
        if (fullLeftJSON != null && fullLeftJSON.length > 0) {
          var groupId = fullLeftJSON[0].group_id;
          $.each(
            fullLeftJSON,
            $.proxy(function (index, item) {
              if (item.name == "order_number") {
                this.quotationNumber = item.value;
              }
              var chkGroup = $.grep(
                this.LeftViewData,
                function (element, index) {
                  return element.group_id == groupId;
                }
              );
              if (chkGroup.length == 0) {
                var returnedData = $.grep(
                  fullLeftJSON,
                  function (element, index) {
                    return element.group_id == groupId;
                  }
                );
                if (returnedData.length > 0) {
                  var objGroup = {
                    group_id: groupId,
                    group_name: returnedData[0].group_name,
                    LeftViewData: returnedData,
                  };
                  vm.LeftViewData.push(objGroup);
                }
              }
              groupId = item.group_id;
            }, vm)
          );
        }
        vm.isLoading=false;
        setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
        setTimeout(function(){vm.SetResponsiveTab()}, 200);
        setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
      });
    },
  
  
    changeDateForAddNoteUsrEnd: function (value) {
      try {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
        var getDate = new Date(
          moment(String(value)).year(),
          moment(String(value)).month(),
          moment(String(value)).date(),
          moment(String(value)).hours(),
          moment(String(value)).minutes()
        );
        if (userTimeZone[0].indexOf("+") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1]))
          );
        } else if (userTimeZone[0].indexOf("-") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1]))
          );
        }
        return moment(getDate, "YYYY-MM-DD HH:mm").format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      } catch (e) {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
        return moment(String(value)).format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      }
    },
    openNoteDialogue() {
      this.isShowNoteDialogue = this.isShowNoteDialogue ? false : true;
    },
    openEmailModal(item) {
      var htmlResult=$("#invoiceSendMail").html();
      this.EmailResultBody=htmlResult;
      this.sendEmailModal = true;
      this.modalItem = item;
    },
    openAddTaskModel() {
      this.showAddTaskModal = this.showAddTaskModal ? false : true;
    },
    openAddNewEvent() {
      this.showAddEventModal = this.showAddEventModal ? false : true;
    },
    openCallEvent() {
      this.showCallModal = this.showCallModal ? false : true;
    },
    openActivityModal(item) {
      if (item.activity_type == "Task") {
        this.showAddTaskModal = true;
        this.modalTaskItem = item;
      } else if (item.activity_type == "FollowUp") {
        this.showAddEventModal = true;
        this.modalFollowUpItem = item;
      } else if (item.activity_type == "Call") {
        this.showCallModal = true;
        this.modalCallItem = item;
      }
    },
    DeleteActivity(item) {
      this.modalDeleteItem = item;
      this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
    },
    openContactModal(contactDetails) {
      this.iscontactDetails = contactDetails;
      this.showContactModal = this.showContactModal ? false : true;
    },
    DeleteActivityData: function (id) {
      var vm = this;
      if (id > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
          var postJSON = {
            ids: id,
            moduleName: "CRM",
            subModuleCode: vm.$route.params.subModuleCode,
            refCode: "DELETE_VIEW_ACTIVITY",
            refId: vm.$route.params.id,
          };
          var objectPostString = JSON.stringify(postJSON);

          LeadDataService.CommonDelete(objectPostString).then(function (
            response
          ) {
                if (response.data[0].Status == "Success") {
              vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Activity')]), "success", true, vm.$t("Alert"));
              vm.loadData("activities");
            } else {
              vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Activity')]), "warning", true, vm.$t("Alert"));
            }
             
          });
        });
      }
    },
    openShowNotePopup: function () {
      var ths = this;
      if (
        typeof ths.checkForAdditionalPermission !== "undefined" &&
        !ths.checkForAdditionalPermission("AddNote")
      ) {
        ths.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          ths.$t("Alert")
        );
      } else {
        ths.isShowNotePopup = true;
        $("#tab-notes").trigger("click");
        ths.loadData("notes");
      }
    },
    deleteContactFromTab(value) {
      var vm = this;
      if (value > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
          var postJSON = {
            ids: value,
            moduleName: "CRM",
            subModuleCode: vm.$route.params.subModuleCode,
            refCode: "DELETE_VIEW_CONTACTS",
            refId: vm.$route.params.id,
          };
          var objectPostString = JSON.stringify(postJSON);

          LeadDataService.CommonDelete(objectPostString).then(function (
            response
          ) {
            var box = "";
            if (response.data[0].Status == "Success") {
              $(".chkItems:checkbox:checked")
                .prop("checked", false)
                .removeAttr("checked");
              vm.loadData("contacts");
              vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("Contact")]),'success',true,vm.$t('Alert'));
            } else {
              vm.ShowAlert(vm.$t("DeletedError", [vm.$t("Contact")]),'failure',true,vm.$t('Alert'));
            }
            setTimeout(function () {
              box.modal("hide");
            }, 2000);
             
          });
        });
      } else {
        vm.ShowAlert(vm.$t("DeletedError", [vm.$t("Contact")]),'warning',true,vm.$t('Alert'));
      }
    },
    formatDateTime(value) {
      if (value) {
        try {
          var timeFormat = Globalize.culture().calendar.patterns.t;
          timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
          var getDate = new Date(
            moment(String(value)).year(),
            moment(String(value)).month(),
            moment(String(value)).date(),
            moment(String(value)).hours(),
            moment(String(value)).minutes()
          );
          return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(
            String(Globalize.culture().calendar.patterns.d).toUpperCase() +
              " " +
              String(timeFormat)
          );
        } catch (e) {
          var timeFormat = Globalize.culture().calendar.patterns.t;
          timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
          return moment(String(value)).format(
            String(Globalize.culture().calendar.patterns.d).toUpperCase() +
              " " +
              String(timeFormat)
          );
        }
      }
    },
    loadData(tabName, callFrom) {
      var vm = this;
      vm.currentTab = tabName;
      vm.DisableButton();
      if (tabName == "notes") {
        vm.isNotes = true;
        vm.activeKey = 2;
        if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
      } else if (tabName == "attachments") {
        vm.isAttachment = true;
        vm.activeKey = 3;
      } else if (tabName == "activities") {
        vm.isTask = true;
        vm.isScheduleCall = true;
        vm.isFollowup = true;
        vm.activeKey = 4;
      } else if (tabName == "emailLog") {
        vm.isSendEmail = true;
        vm.activeKey = 6;
      } else if (tabName == "calls") {
         vm.activeKey = 8;
         vm.isProduct = true;
      } else if (tabName == "GIN") {
        vm.activeKey = 7;
        vm.isProduct = true;
      }
      if (tabName != "" && tabName != null) {        

        if (tabName == "attachments") {
          LeadDataService.AttachmentListing(vm.ApiURL1).then(function (
            response
          ) {
             
            if (response.data != null) {
              vm.attachmentData = response.data.data;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "notes") {
          this.noteData = [];
          LeadDataService.NoteListing(vm.ApiURL1).then(function (response) {
             
            if (response.data != null && response.data.data.length > 0) {
              vm.noteData = response.data.data;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "activities") {
          LeadDataService.ActivityListing(vm.ApiURL1).then(function (response) {
             
            if (response.data != null && response.data.data.length > 0) {
              vm.activitiesData = response.data.data;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              setTimeout(function(){vm.ResponsiveDataTable('table-openactivities-content')},
                500
              );
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "products") {
          var productrequrl = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
          LeadDataService.getItemListForAssociate(productrequrl).then(function (
            response
          ) {
            if (response.data != null) {
              vm.productData = response.data.data;
              vm.productHeaders = response.data.schema;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              vm.productData = null;
            }
          });
          vm.activeKey = 5;
        } else if (tabName == "calls") {
          url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
          LeadDataService.GetCallLogsListinge(url).then(function (response) {
             
            if (response.data != null) {
              vm.callData = response.data.data;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              vm.calltData = null;
            }
          });
          vm.activeKey = 9;
        } else if (tabName == "GIN") {
          url = `sourceId=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&moduleName=${vm.$route.params.moduleName}`;
          LeadDataService.GetGoodInwardForPurchaseOrder(url).then(function (
            response
          ) {
             
            vm.ginData = [];
            if (response.data != null && response.data.data.length > 0) {
              var fullJSON = response.data.data;
              var groupBy = "";
              var gin_id = "";
              var gin_status = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gin_id = item.GIN_ID;
                  groupBy = item.GIN_NUMBER;
                  gin_status = item.GIN_STATUS;
                  var chkGroup = $.grep(vm.ginData, function (element, index) {
                    return element.gin_id == gin_id;
                  });
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return element.GIN_ID == gin_id;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        gin_id: gin_id,
                        gin_status: gin_status,
                        gin_number: groupBy,
                        groupData: returnedData,
                      };
                      vm.ginData.push(objGroup);
                    }
                  }
                }, vm)
              );
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              setTimeout(function(){vm.ResponsiveDataTable('table-GIN-content')}, 500);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              vm.ginData = null;
            }
          });
          vm.activeKey = 8;
        } else if (tabName == "timeline") {
          url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
          LeadDataService.TimelineListing(url).then(function (response) {
             
            if (response.data != null) {
              vm.timelinedata = [];
              var fullJSON = response.data.data;
              var gruopBy = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                  var chkGroup = $.grep(
                    vm.timelinedata,
                    function (element, index) {
                      return element.groupKey == gruopBy;
                    }
                  );
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        groupKey: gruopBy,
                        groupData: returnedData,
                      };
                      vm.timelinedata.push(objGroup);
                    }
                  }
                }, vm)
              );
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
          vm.activeKey = 1;
        } else if (tabName == "emailLog") {
          var url =
            vm.EmailListingApiURL +
            vm.SortBy +
            "&sortExp=" +
            vm.SortExp +
            "&pageSize=" +
            vm.PageSize +
            "&pageNumber=" +
            vm.PageNumber;
          LeadDataService.EmailListing(url).then(function (response) {   
            vm.GetSalesOrderPreview();          
            if (
              response.data != null &&
              response.data != "" &&
              response.data.data.length > 0
            ) {
              vm.EmailLogData = response.data.data;
              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
              setTimeout(function(){vm.ResponsiveDataTable('table-emails-log-content')},
                500
              );
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        }
         else if (tabName == "email") {
          vm.GetSalesOrderPreview();
          vm.activeKey = 7;                  
        }
      }
    },
    GetSalesOrderPreview(){
      var vm=this;
     var url = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&type=PURCHASE_ORDER_TEMPLATE`;           
          LeadDataService.getDDLData(url).then(function (response) {
            var json = response.data;
            if (json != null) {
              vm.TemplateName = json.DATA[0].name;
              if (vm.TemplateName == "Bill1") {
                vm.template1 = true;
                vm.template2 = false;
                vm.template3 = false;
                vm.template4 = false;
              } else if (vm.TemplateName == "Bill2") {
                vm.template1 = false;
                vm.template2 = true;
                vm.template3 = false;
                vm.template4 = false;
              } else if (vm.TemplateName == "Bill3") {
                vm.template1 = false;
                vm.template2 = false;
                vm.template3 = true;
                vm.template4 = false;
              } else if (vm.TemplateName == "Bill4") {
                vm.template1 = false;
                vm.template2 = false;
                vm.template3 = false;
                vm.template4 = true;
              }
               
              if (vm.TemplateName != null) {
                var url =`id=${vm.$route.params.id}&moduleCode=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}`;
                LeadDataService.QuotationDetails(url).then(function (
                  response
                ) {
                  var addressData = response.data.contact_person;
                  vm.PreviewData = response.data;
                  if (response.data != null) {
                    var productSummaryData = [];
                    var i = 1;
                    $.each(
                      response.data.dataproductlineitemjson,
                      function (key, item) {
                        var prductName = "";
                        var prductId = 0;
                        if (item.PRODUCT_NAME != null) {
                          try {
                            item.PRODUCT_NAME = JSON.parse(item.PRODUCT_NAME);
                          } catch (ex) {}
                          if (Array.isArray(item.PRODUCT_NAME)) {
                            prductName = item.PRODUCT_NAME[0].name;
                            prductId = item.PRODUCT_NAME[0];
                          } else {
                            prductName = item.PRODUCT_NAME;
                          }
                        } else {
                          prductName = "";
                        }
                        var dataObject = {
                          SNo: i,
                          ProductId: prductId,
                          ProductName: prductName,
                          ProductList: item.ITEM_PRODUCT_LIST_PRICE,
                          ProductQuantity: item.ITEM_PRODUCT_QUANTITY,
                          ProductAmount: item.ITEM_PRODUCT_AMOUNT,
                          ProductDiscount: item.ITEM_PRODUCT_DISCOUNT,
                          ProductDesc: item.ITEM_PRODUCT_DESC,
                          ProductTax:
                            JSON.parse(item.TAX_DATA) == null
                              ? 0
                              : (item.ITEM_PRODUCT_AMOUNT *
                                  JSON.parse(item.TAX_DATA)[0].TAX_RATE) /
                                100,
                          ProductTotal: item.ITEM_TOTAL,
                        };
                        productSummaryData.push(dataObject);
                        i++;
                      }
                    );
                    if (productSummaryData.length > 0) {
                      vm.SummaryData = productSummaryData;
                    } else {
                      vm.SummaryData = "";
                    }
                    if (
                      addressData[0] !== undefined &&
                      addressData[0] != null
                    ) {
                      vm.orderNumber =
                        addressData[0].ORDER_NUMBER == null
                          ? ""
                          : addressData[0].ORDER_NUMBER;
                      vm.createdAt =
                        addressData[0].CREATED_AT == null
                          ? ""
                          : addressData[0].CREATED_AT;
                      vm.companyName =
                        addressData[0].COMPANY_NAME == null
                          ? ""
                          : addressData[0].COMPANY_NAME;
                      vm.address =
                        addressData[0].ADDRESS == null
                          ? ""
                          : addressData[0].ADDRESS;
                      vm.email =
                        addressData[0].EMAIL == null
                          ? ""
                          : addressData[0].EMAIL;
                      vm.phoneNo =
                        addressData[0].PHONE_NUMBER == null
                          ? ""
                          : addressData[0].PHONE_NUMBER;
                      vm.clientName =
                        addressData[0].ACCOUNT_NAME == null
                          ? ""
                          : addressData[0].ACCOUNT_NAME;
                      vm.billingContactPerson =
                        addressData[0].CONTACT_PERSON_NAME == null
                          ? ""
                          : addressData[0].CONTACT_PERSON_NAME;
                      vm.billingAccountAddress =
                        addressData[0].ACCOUNT_ADDRESS == null ||
                        addressData[0].ACCOUNT_ADDRESS == ""
                          ? ""
                          : addressData[0].ACCOUNT_ADDRESS + ",";
                      vm.billingCountryData =
                        addressData[0].COUNTRY_NAME == null ||
                        addressData[0].COUNTRY_NAME == ""
                          ? ""
                          : addressData[0].COUNTRY_NAME + ",";
                      vm.billingStateData =
                        addressData[0].STATE_NAME == null ||
                        addressData[0].STATE_NAME == ""
                          ? ""
                          : addressData[0].STATE_NAME + ",";
                      vm.billingCityData =
                        addressData[0].CITY_NAME == null ||
                        addressData[0].CITY_NAME == ""
                          ? ""
                          : addressData[0].CITY_NAME + ",";
                      vm.billingCountyData =
                        addressData[0].COUNTY_NAME == null ||
                        addressData[0].COUNTY_NAME == ""
                          ? ""
                          : addressData[0].COUNTY_NAME + ",";
                      vm.billingZipCodeData =
                        addressData[0].ZIP_CODE_NAME == null ||
                        addressData[0].ZIP_CODE_NAME == ""
                          ? ""
                          : addressData[0].ZIP_CODE_NAME + ",";
                      vm.billingEmailData =
                        addressData[0].EMAIL_ID == null
                          ? ""
                          : addressData[0].EMAIL_ID;
                      vm.billingUserPhoneData =
                        addressData[0].MOBILE_PHONE == null
                          ? ""
                          : addressData[0].MOBILE_PHONE;
                      vm.orderTitle =
                        addressData[0].ORDER_TITLE == null
                          ? ""
                          : addressData[0].ORDER_TITLE;
                      vm.description =
                        addressData[0].DESCRIPTION == null
                          ? ""
                          : addressData[0].DESCRIPTION;
                      vm.termAndCondition =
                        addressData[0].TERM_AND_CONDITIONS == null
                          ? ""
                          : addressData[0].TERM_AND_CONDITIONS;
                      vm.billingFullAddress =
                        vm.billingAccountAddress +
                        vm.billingCountryData +
                        vm.billingCityData +
                        vm.billingZipCodeData;
                      vm.billingFullAddress = vm.removeLastChar(
                        vm.billingFullAddress,
                        ","
                      );
                    }
                    if (
                      addressData[1] !== undefined &&
                      addressData[1] != null
                    ) {
                      vm.shippingContactPerson =
                        addressData[1].CONTACT_PERSON_NAME == null
                          ? ""
                          : addressData[1].CONTACT_PERSON_NAME;
                      vm.clientName =
                        addressData[1].ACCOUNT_NAME == null
                          ? ""
                          : addressData[1].ACCOUNT_NAME;
                      vm.shippingAccountAddress =
                        addressData[1].ACCOUNT_ADDRESS == null ||
                        addressData[1].ACCOUNT_ADDRESS == ""
                          ? ""
                          : addressData[1].ACCOUNT_ADDRESS + ",";
                      vm.shippingCountryData =
                        addressData[1].COUNTRY_NAME == null ||
                        addressData[1].COUNTRY_NAME == ""
                          ? ""
                          : addressData[1].COUNTRY_NAME + ",";
                      vm.shippingStateData =
                        addressData[1].STATE_NAME == null ||
                        addressData[1].STATE_NAME == ""
                          ? ""
                          : addressData[1].STATE_NAME + ",";
                      vm.shippingCountyData =
                        addressData[1].COUNTY_NAME == null ||
                        addressData[1].COUNTY_NAME == ""
                          ? ""
                          : addressData[1].COUNTY_NAME + ",";
                      vm.shippingCityData =
                        addressData[1].CITY_NAME == null ||
                        addressData[1].CITY_NAME == ""
                          ? ""
                          : addressData[1].CITY_NAME + ",";
                      vm.shippingZipCodeData =
                        addressData[1].ZIP_CODE_NAME == null ||
                        addressData[1].ZIP_CODE_NAME == ""
                          ? ""
                          : addressData[1].ZIP_CODE_NAME + ",";
                      vm.shippingEmailData =
                        addressData[1].EMAIL_ID == null
                          ? ""
                          : addressData[1].EMAIL_ID;
                      vm.shippingUserPhoneData =
                        addressData[1].MOBILE_PHONE == null
                          ? ""
                          : addressData[1].MOBILE_PHONE;
                      vm.shippingFullAddress =
                        vm.shippingAccountAddress +
                        vm.shippingCountryData +
                        vm.shippingCityData +
                        vm.shippingZipCodeData;
                      vm.shippingFullAddress = vm.removeLastChar(
                        vm.shippingFullAddress,
                        ","
                      );
                    }
                    var amount = response.data.dataproductlineitemjsonamount;
                    var jsonAmount =
                      response.data.dataproductlineitemjsonamount[0];
                    if (
                      amount !== undefined &&
                      amount != null &&
                      jsonAmount !== undefined &&
                      jsonAmount != null
                    ) {
                      vm.subtotal =
                        jsonAmount.summary_sub_total == null
                          ? ""
                          : jsonAmount.summary_sub_total;
                      vm.shippingCharges =
                        jsonAmount.summary_shipping_charges == null
                          ? ""
                          : jsonAmount.summary_shipping_charges;
                      vm.adjustments =
                        jsonAmount.summary_adjustments == null
                          ? ""
                          : jsonAmount.summary_adjustments;
                      vm.netTotal =
                        jsonAmount.summary_total == null
                          ? ""
                          : jsonAmount.summary_total;
                    }
                    vm.CRMPurchaseOrder = true;
                    vm.noRecordForTab = false;
                  } else {
                    vm.CRMPurchaseOrder = false;
                    vm.noRecordForTab = true;
                  }
                   
                });
              }
            } else {
               
              vm.noRecordForTab = true;
            }
          });
    },
    removeLastChar: function (value, char) {
      var lastChar = value.slice(-1);
      if (lastChar == char) {
        value = value.slice(0, -1);
      }
      return value;
    },
    DownlaodPDF:function(){
            var vm=this;
            var postJSON = {
                    moduleName: vm.$route.params.moduleName,
                    subModuleCode: vm.$route.params.subModuleCode,
                    refId: vm.$route.params.id,
                    companyLogo: vm.$refs.purchaseorder.companyLogo,
                    quoteCompanyLogo: 'quotationLogo',
                };
                vm.isLoading=true;
                var objectPostString = JSON.stringify(postJSON);
                LeadDataService.DownlaodPDF(objectPostString).then(function(response){
                var result = response.data;
                const linkSource = `data:${result.fileType};base64,${result.bytedata}`;
                const downloadLink = document.createElement("a");
                downloadLink.href = linkSource;
                downloadLink.download = result.fileName;
                downloadLink.click();
                vm.isLoading=false;
            }); 
        }
  },
 };
</script>
<template>
