 && item.picklist_options != 'Lookup' && ((item.name != 'description' && item.name != 'term_and_conditions') 
|| ((item.name == 'description' && item.name == 'term_and_conditions') && isBlank(item.value)))">{{ isBlank(item.value)?"--":(item.value)}}</span>
                                      <crmreadmore callFrom="PurchaseOrderDetails" v-if="(item.name == 'description' || item.name == 'term_and_conditions') && !isBlank(item.value)" v-bind:data="item.value" :heading="$t('Description')" length="100" ></crmreadmore>
                                      <span class="answer rightlabeltext ml-1"  v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">
                                        {{isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}
                                      </span>
                                    </label>
                                  </div>
                                </div>
