<template>
    <div class="modal d-block my-popups dialog">
        <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title"> {{ $t('DeployItems') }}</h5>
                    </slot>
                    <button v-on:click="$emit('close')" type="button" title="" class="close" data-original-title="Close"><em
                            class="fa fa-times" aria-hidden="true"></em></button>
                </div>
                <div class="modal-body">
                    <form>
                        <div class="d-flex flex-wrap">
                            <div class="col-md-12 p-0">
                                <div class="row">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <!-- <div class="form-group">
                                                <label>{{ $t('Location') }}:<span class="text-danger">*</span></label>
                                                <tree-view v-model="data.fieldInfo.value" :options="options"
                                                                v-on:input="BindItemTypeByUserId(data.fieldInfo.value)"
                                                                placeholder="Select Location"
                                                                :disabled="treeViewDisabled" :show-count="true"
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                            </tree-view>
                                                 <div class="row">
                                                    <input type="hidden" id="hdnCompantLocationSelect">
                                                    <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left" id="divmultilevelselect1" v-on:click="OnChangeLocation()">
                                                    </div>
                                                    <small v-if="validatelocation" class="float-left text-danger col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left font-weight-light">Please select the Location</small>
                                                </div> 
                                            </div> -->
                                        <div class="form-group">
                                            <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                            <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                                v-slot="{ errors }">
                                                <tree-view v-model="TreeValue" :options="options"
                                                    placeholder="Select Location" :show-count="true"
                                                    :normalizer="normalizeOption" :disable-branch-nodes-without-children="true"
                                                    v-bind:class="{ 'is-invalid': errors[0] }">
                                                </tree-view>
                                                <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                                    errors[0]
                                                }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                </div>
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" class="btn btn-success formbtn text-white mr-1"
                                            v-on:click="saveDeployItems()"><em class="fa fa-save pr-2"></em>{{ $t('Save')
                                            }}</a>
                                        <a v-on:click="$emit('close')"
                                            class="btn btn-danger  delete-btn formbtn widthhalf text-white"><em
                                                class="fa fa-close pr-2"></em>{{ $t('Cancel') }}</a>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from "../../../services/DataService";
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css';
export default {
    components: {

        'tree-view': Treeselect
    },
    data() {
        return {
            isLoading: false,
            TreeValue: null,
            options: []
        }
    },
    created() {
        this.isLoading = true;
        this.GetLocationByCompanyId();
    },
    methods: {
        GetLocationByCompanyId: function () {
            var vm = this;
            var url = 'reqForm='
            DataService.GetMultiLocationByCompanyId(url)
                .then(function (response) {
                    vm.isLoading = false;
                    if (response.data.length > 0) {
                        vm.FindalLocationArray = [];
                        response.data.forEach((value) => {
                            var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                                return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                            });
                            if (CheckIfExist.length == 0) {
                                if (value.PARENT_LOCATION_ID == null) {
                                    let locationId = value.COMPANY_LOCATION_ID
                                    let rslt = vm.getChildLocation(locationId, value, response.data)
                                    vm.FindalLocationArray.push(rslt)
                                }
                            }
                        });                        
                        vm.options = vm.FindalLocationArray;
                    }
                });

        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        normalizeOption(option) {
            let label = option.label;
            if (option.children && option.children.length === 0) {
                label += ' (No Children Location available)';
            }
            return {
                id: option.id,
                label: label,
                children: option.children || []                
            };
        },
        saveDeployItems: function () {
            this.$parent.commonApprove(this.TreeValue);
        },
        // OnChangeLocation: function () {
        //     var vm = this;
        //     vm.isLoading = true;
        //     setTimeout(function () {
        //         $("#divmultilevelselect1").find('ul#CompantLocationSelect').css('display', 'block');
        //         vm.CompantLocationSelect = $('#hdnCompantLocationSelect').val();
        //         if (vm.CompantLocationSelect != null && vm.CompantLocationSelect != "") {
        //             $("#divmultilevelselect1").find('ul#CompantLocationSelect').css('display', 'none');
        //             vm.validatelocation = false;
        //             $(".CompantLocationSelectdd-container").find("div.btn").removeClass("form-control is-invalid");
        //         }
        //         vm.isLoading = false;
        //     }, 300);
        // },
    }
}

</script>